declare var require;
var parser = require('../parsers/axis');
import stm = require('./MdxStatement');

export enum MdxAxisSpecType {
    ROWS, COLUMNS
}

export default class MdxAxisSpec implements stm.MdxParseable {

    constructor(public type: MdxAxisSpecType, public list: any[]) {
    }

    static parse(mdx:string):MdxAxisSpec {
        return parser.parse(mdx, MdxAxisSpec, MdxAxisSpecType);
    }

    mdx():string {
        return '';
    }
}
